:root {
  --base: #ffc600;
  --header-height: 65px;
  --header-padding: 16px;
  --padding-generic: 4%;
  --padding-small: 2%;

  --tile-text-color: #ffffff;
  --key-text-color: #000000;
  --key-text-color-dk:#fff;

  --color-tone-1: #787c7e;
  --color-tone-2: #878a8c;
  --color-tone-3: #edeff1;
  --color-tone-4: #d3d6da;
  --color-tone-5: #ffffff;


  --color-present: #c9b458;
  --color-absent: #787c7e;
  --color-correct: #6aaa64;

  --square-width: 340px;

  --flip-duration: 250ms;

}

body {
  box-sizing: border-box;
}




.App {
  text-align: center;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: var(--header-padding);
  height: var(--header-height);
  color: #000000;
  border-bottom: 1px solid var(--color-tone-4);
}

.header h1 {
  font-weight: 700;
  font-size: 28px;
  letter-spacing: .01em;
  text-align: left;
}

.header span {
  color: var(--color-correct);
}

.main {
  width: 90%;
  display: flex;
  flex-direction: row;
  min-height: calc(90vh - var(--header-height));
  margin: 2%;
  flex-wrap: wrap;
  gap: 0.5rem;
  padding: var(--padding-small);
  /*border: solid black 4px;*/
}

.game {
  display: flex;
  flex-direction: column;
  min-width: 68%;
  max-width: 68%;
  flex-grow: 2;
  flex-basis: 0;
  /*border: 1px solid black;*/
  align-items: center;
  justify-content: center;
}

.sidepanel {
  flex-grow: 1;
  max-width: 30%;
  border: 1px solid var(--color-tone-2);
  border-radius: 4px;
}

.square {
  display: inline-flex;
  width: 100%;
  height: 100%;
  border: 2px solid var(--color-tone-3);
  font-weight: bold;
  font-size: 2rem;
  justify-content: center;
  align-items: center;
  color: var(--key-text-color);
  text-transform: uppercase;
  user-select: none;

}

.square.tbd {
  background-color: var(--color-tone-5);
  border: 2px solid var(--color-tone-2);
  color: var(--key-text-color);
  animation-name: popin;
  animation-duration: 100ms;
}

.square[data-flip='out']{
  animation-name: flipout;
  animation-duration: var(--flip-duration);
  animation-timing-function: ease-in;
}

.square[data-flip='in']{
  animation-name: flipin;
  animation-duration: var(--flip-duration);
  animation-timing-function: ease-in;
}

.square.absent[data-flip='in'], .key.absent {
  background-color: var(--color-absent);
  color: var(--tile-text-color);
}

.square.present[data-flip='in'], .key.present {
  background-color: var(--color-present);
  color: var(--tile-text-color);
}

.square.correct[data-flip='in'], .key.correct {
  background-color: var(--color-correct);
  color: var(--tile-text-color);
}



.row {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 5px;
}

.row.invalid, .row.incomplete {
  animation-name: rowshake;
  animation-duration:calc(var(--flip-duration)*1.5);
}


.board {
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 5px;
  padding: 10px;
  width: var(--square-width);
  height: calc(var(--square-width) + 60px);
  border-bottom: 1px solid lightblue;
  padding-bottom: var(--padding-generic);
}

.keyboard {
  height: 200px;
  margin: 0 8px;
  user-select: none;
  display: grid;
  padding-top: var(--padding-generic);
  padding-bottom: var(--padding-generic);
}

.keyRow {
  display: flex;
  width: 100%;
  margin: 0 auto 8px;
}

.key {
  font-size: 1.25em;
  font-weight: bold;
  border: 0;
  padding: 0;
  margin: 0 6px 0 0;
  height: 60px;
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
  background-color: var(--color-tone-4);
  color: var(--key-text-color);
  flex: 1;
  display: flex;
  min-width: 32px;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}

button:focus{
  outline: none;
  box-shadow: none;
}


.keySpacer {
  flex: .5;
  visibility: hidden;
}

.keyboard button[data-key="Enter"]{
  padding: 0 4px 0 4px;
  font-size: 0.75em;
}

.keyboard button[data-key="âŒ«"]{ font-size: 1em; }

.wordlist {
  display: flex;
  max-height: 100%;
  margin-left: 25px;
  z-index: 0;
  overflow-x: auto;
  overflow-y: auto;
  scroll-margin: 0;
  scroll-behavior: smooth;
  overscroll-behavior: contain;
}

.wordlist-items {
  display: flex;
  min-width: 35%;
  flex-flow: column wrap;
  align-content: flex-start;
  height: calc(100% - 30px);
  padding: 0 0 24px;
}

ul.wordlist-items {
  list-style-type: none;
}

.wordlist-items>li {
  padding: 2px 0 2px 1px;
  margin-bottom: 5px;
  border-bottom: 1px solid var(--color-tone-2);
  font-size: 0.85em;
}

.wordlist-items>li:nth-child(odd){
  animation-name: flyin-north;
  animation-duration: calc(var(--flip-duration)*4);
}
.wordlist-items>li:nth-child(even){
  animation-name: flyin-south;
  animation-duration: calc(var(--flip-duration)*4);
}

.wordlist-info {
  padding: 10px 0 8px 5px;
  font-size: 0.81em;
  font-weight: bold;
  display: block;
  width: calc(100% - 5px);
  border-bottom: 1px solid var(--color-tone-4);
}

.wordlist-info>span {
  font-weight: 400;
  font-size: 0.79em;
}



@keyframes rowshake {

  10%,90% {
    transform: translateX(-1px)
  }

  20%,80% {
    transform: translateX(2px)
  }

  30%,
  50%,
  70% {
    transform: translateX(-4px)
  }

  40%,
  60% {
    transform: translate(4px)
  }
}

@keyframes popin{
  from{
    transform: scale(0.8);
    opacity: 0;
  }

  40% {
    transform: scale(1.1);
    opacity: 1;
  }
}

@keyframes flipin {
  from { transform: rotateX(-90deg); }
  to{ transform: rotateX(0deg);}
  
}
@keyframes flipout {
  from { transform: rotateX(0deg);}

  to{ transform: rotateX(-90deg);}  
}

.MuiSnackbarContent-message {
  text-align: center;
}

@keyframes flyin-south{
  from{
    transform: scale(10.0) translate(120%,0%);
  }
  to { 
    transform: scale(1.0) translate(0%,0%);
  }
}

@keyframes flyin-north{
  from{
    transform: scale(10.0) translate(-120%,-155%);
  }
  to { 
    transform: scale(1.0) translate(0%,0%);
  }
}


@media (prefers-color-scheme: dark){
  body {
    background-color: black;
  }
  .header{
    color: white;
    border-bottom: 1px solid var(--color-tone-3);
  }

  .header h1{
    color: white;
  }


  .header span{
    color: var(--base);
  }

  .sidepanel{
    color: var(--color-tone-3);
  }

  .square{
    border-color: var(--color-tone-1);
  }

  .square.tbd{
    background-color: #000000;
    color:#fff;
  }

  .key{
    color: var(--key-text-color-dk);
    background-color: #878a8c;
  }

  .key.absent{
    background-color: #262326;
  }

  .wordlist-items>li {
    color: var(--color-tone-5);
    border-bottom: 1px solid var(--color-tone-5);
  }

  .wordlist-info{
    color: var(--color-tone-3);
  }

}


